.login-block {
  width: 420px;
  padding: 20px;
  background: #f2f2f2;
  border-radius: 5px;
  border-top: 5px solid #00A0F2;
  margin: 0 auto;
  margin-top: 100px;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(62, 62, 62, 0.24);
}
.login-block h1 {
  text-align: center;
  color: #030303;
  font-size: 18px;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 20px;
}
.login-block input {
  width: 100%;
  height: 42px;
  box-sizing: border-box;
  border-radius: 5px;
  border: 1px solid #ccc;
  margin-bottom: 20px;
  font-size: 14px;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  padding: 0 20px 0 50px;
  outline: none;
}
.login-block input#username {
  background: #fff url(/security-bundle/assets/images/icons_login_edit.png) 20px top no-repeat;
  background-size: 16px 80px;
}
.login-block input#username:focus {
  background: #fff url(/security-bundle/assets/images/icons_login_edit.png) 20px bottom no-repeat;
  background-size: 16px 80px;
}
.login-block input#passwd {
  background: #fff url(/security-bundle/assets/images/secure_login_edit.png) 20px top no-repeat;
  background-size: 16px 80px;
}
.login-block input#passwd:focus {
  background: #fff url(/security-bundle/assets/images/secure_login_edit.png) 20px bottom no-repeat;
  background-size: 16px 80px;
}
.login-block input:active,
.login-block input:focus {
  border: 1px solid #00A0F2;
}
.login-block button {
  width: 100%;
  height: 40px;
  background: #00A0F2;
  box-sizing: border-box;
  border-radius: 5px;
  border: 1px solid #9acfea;
  color: #fff;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 14px;
  font-family: 'Lucida Grande', 'Lucida Sans Unicode', arial, sans-serif;
  outline: none;
  cursor: pointer;
}
.login-block button:hover {
  background: #00A0F2;
}
.login-block button:hover,
.login-block button:active,
.login-block button:focus {
  background: #0193f2;
}
